const API_KEY = "e925287d0746c96ca5119cb54a7722bf";
const BASE_PATH = "https://api.themoviedb.org/3";

interface IMovie {
  id: number;
  backdrop_path: string;
  poster_path: string;
  title: string;
  overview: string;
}

export interface IGetMovieResult {
  dates: {
    maximum: string;
    minimum: string;
  };
  page: number;
  results: IMovie[];
  total_pages: number;
  total_results: number;
}

export function getMovies() {
return fetch(`${BASE_PATH}/movie/now_playing?api_key=${API_KEY}`).then(
(response) => response.json()
);
}

interface ITVShow {
id:number,
name:string,
overview:string,
backdrop_path?:string
};

export interface IGetTVShowResult{
page:number,
results:Array<ITVShow>,
total_pages:number,
total_results:number
};

export function getTrendingTVShows(){
return fetch(`${BASE_PATH}/trending/tv/week?api_key=${API_KEY}`).then(
(response)=>response.json()
);
}
export function getLatestMovies() {
  return fetch(`${BASE_PATH}/movie/popular?api_key=${API_KEY}`).then(
    (response) => response.json()
  );
}